:root {
  --rad: .7rem;
  --dur: .3s;
  --color-dark: #2f2f2f;
  --color-light: #fff;
  --color-brand: #57bd84;
  --font-fam: "Lato", sans-serif;
  --height: 5rem;
  --btn-width: 6rem;
  --bez: cubic-bezier(0, 0, .43, 1.49);
}

body {
  background: #e6e6fa;
  padding: 200px 0;
  font-family: sans-serif;
  font-size: 12px;
}

html {
  box-sizing: border-box;
  height: 100%;
  font-size: 10px;
}

*, :before, :after {
  box-sizing: inherit;
}

form {
  width: 30rem;
  background: var(--color-brand);
  border-radius: var(--rad);
  margin: 0 auto 200px;
  position: relative;
}

input, .btnGo {
  height: var(--height);
  font-family: var(--font-fam);
  color: var(--color-dark);
  border: 0;
  font-size: 1.8rem;
}

input[type="text"] {
  width: 100%;
  background: var(--color-light);
  border-radius: var(--rad);
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  transition: all var(--dur) var(--bez);
  z-index: 1;
  outline: 0;
  padding: 0 1.6rem;
  transition-property: width, border-radius;
  position: relative;
}

.btnGo {
  width: var(--btn-width);
  background: var(--color-brand);
  border-radius: 0 var(--rad) var(--rad) 0;
  font-weight: bold;
  display: block;
  position: absolute;
  top: 0;
  right: 0;
}

input:not(:placeholder-shown) {
  border-radius: var(--rad) 0 0 var(--rad);
  width: calc(100% - var(--btn-width) );
}

label {
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px;
  width: 1px;
  border: 0;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

p {
  margin: 0;
  padding: 0;
}

img {
  width: 300px;
  height: 200px;
  display: block;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.info {
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  display: flex;
}

.info-item {
  text-align: center;
}

.photo-card {
  background: #fff;
  border: 1px solid #eee;
  flex-basis: calc(25% - 15);
  margin-bottom: 20px;
  margin-right: 20px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.photo-card:nth-child(4n) {
  margin-right: 0;
}

.photo-card:nth-last-child(-n+4) {
  margin-bottom: 0;
}

.btnLoadMore {
  width: 400px;
  height: 50px;
  color: var(--color-dark);
  background-color: #57bd84;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 5px;
  margin: 100px auto 0;
  font-size: 1.8rem;
  font-weight: bold;
  display: block;
}

.hide {
  display: none;
}

